import React from 'react';
import { Row, Col, Card, Form, Button, InputGroup, FormControl, DropdownButton, Dropdown, FormFile } from 'react-bootstrap';

import Switch from "react-switch";
import Aux from "../../hoc/_Aux";
import AsyncSelect from 'react-select/async'

import { validateItemCreation } from '../../validation/items';
import ErrorBody from '../../App/components/Reusables/ErrorBody';
import { getItemCatagories } from '../../api/inventory/itemCategory';
import Loader from '../../App/components/Reusables/Loader';
import { createItem, getItem, updateItem } from '../../api/inventory/itemApi';

import FileBase64 from 'react-filebase64';
import Jhilikka from '../../App/components/Reusables/Jhilikka';
import DEMO from '../../globals/constant';
import isEmpty from '../../validation/isEmpty';
import ErrorTooltip from '../../App/components/Reusables/ErrorTooltip';
import store from '../../redux/store';
import { SET_FETCH_MORE_OPTIONS, SET_IS_MENU_OPEN } from '../../redux/actions/types';
import CustomSelectMenuList from '../../App/components/Reusables/CustomSelectMenuList';
import CreateItemCategoryModal from '../ItemCategories/CreateItemCategoryModal'


const getBase64 = (file) => new Promise(function (resolve, reject) {
    let reader = new FileReader();
    reader.readAsDataURL(file);
    reader.onload = () => resolve(reader.result)
    reader.onerror = (error) => reject('Error: ', error);
})

const getBase64FromUrl = async url => await fetch(url)
    .then(response => response.blob())
    .then(blob => new Promise((resolve, reject) => {
        const reader = new FileReader()
        reader.onloadend = () => resolve(reader.result)
        reader.onerror = (error) => reject(error)
        reader.readAsDataURL(blob)
    }))

const Update = (props) => {

    const [loading, setLoading] = React.useState(true);
    const [submitting, setSubmitting] = React.useState(false)

    const [defaultItemCategoryOptions, setDefaultItemCategoryOptions] = React.useState([])
    const [itemCategoryOptionsMaxLength, setItemCategoryOptionsMaxLength] = React.useState(0)
    const [searchMode, setSearchMode] = React.useState('static')

    const [createItemCategoryModalData, setCreateItemCategoryModalData] = React.useState(false)


    const [itemData, setItemData] = React.useState({
        name: '',
        barcode: '',
        description: '',
        categoryInputText: '',
        category: '',
        categoryName: '',
        cp: '',
        sp: '',
        weight: '',
        weightUnit: '',
        productImages: [],
        receivedImages: [],
        removedImages: [],
        vatable: false,
        isActive: false,
    })

    const [errors, setErrors] = React.useState({});


    const getItemCategories_ = async (signal) => {
        console.log('api call fired to get items')
        await getItemCatagories({
            "action": 'get',
            "filter": 'none',
            "start": defaultItemCategoryOptions.length,
            "end": defaultItemCategoryOptions.length + DEMO.INSTANCE_PER_LOAD
        }, signal)
            .then(res => {
                console.log('here is one', res);
                if (res.json.status) {
                    setDefaultItemCategoryOptions([
                        ...defaultItemCategoryOptions,
                        ...res.json.item_catagories.map((itm, index) => ({ label: `${itm.name}`, value: itm.id }))
                    ]);
                    setItemCategoryOptionsMaxLength(res.json.count)
                    store.dispatch({
                        type: SET_FETCH_MORE_OPTIONS,
                        payload: ['fetchMoreOptionsItemCategories', false]
                    })
                } else {
                    Jhilikka.fire({ icon: 'error', title: res.json.error, background: '#ffe5de' })
                }
            })
            .catch(err => {
                console.log(err)
            })
    }

    React.useEffect(() => {

        const controller = new AbortController();

        getItemCategories_(controller.signal);

        getItem({ action: 'get', item_id: props.match.params.id }, controller.signal)
            .then(async (res) => {
                console.log(res)
                if (res.json.status) {
                    const receivedItem = res.json.items[0];
                    setItemData({
                        ...itemData,
                        id: receivedItem.id,
                        name: receivedItem.name,
                        barcode: receivedItem.barcode,
                        description: receivedItem.description || '',
                        category: receivedItem.category,
                        categoryName: receivedItem.category_str,
                        cp: receivedItem.average_cost_price,
                        sp: receivedItem.sales_price,
                        weight: receivedItem.weight,
                        weightUnit: receivedItem.weight_unit,
                        receivedImages: receivedItem.images,
                        isActive: receivedItem.is_active,
                        vatable: receivedItem.vat_enabled,
                    })
                    setLoading(false)
                } else { Jhilikka.fire({ icon: 'error', title: res.json.error, background: '#ffe5de' }) }
            }).catch((err) => {
                console.log(err);
            });
        return () => {
            controller.abort()
        }
    }, []);


    React.useEffect(() => {
        const images = [...itemData.productImages];
        if (images.length > 0) {
            console.log('has been triggered')
            const thumbnail = images.find(img => img.category === 'THUMBNAIL');
            console.log('thumbnail is', thumbnail)
            if (!thumbnail) {
                images[0] = { ...images[0], category: 'THUMBNAIL' }
                setItemData({ ...itemData, productImages: images })
            }
        }
    }, [itemData.productImages])

    const loadItemCategoryOptions = async (inputText) => {
        const requestJson = { action: 'get', filter: 'name', name: inputText, start: 0, end: 50 }
        return getItemCatagories(requestJson)
            .then((res) => {
                console.log(res)
                if (res.json.status) {
                    return res.json.item_catagories.map((itm, index) => ({ label: `${itm.name}`, value: itm.id }));
                } else { Jhilikka.fire({ icon: 'error', title: res.json.error, background: '#ffe5de' }) }
            }).catch((err) => {
                console.log(err)
            });
    }

    const _categoryInputChange = (categoryInputText) => {
        setItemData({
            ...itemData,
            categoryInputText
        })
    }

    const _selectedItemCategoryChange = (instance) => {
        setErrors({ ...errors, 'category': '' })
        try {
            setItemData({ ...itemData, category: instance.value, categoryName: instance.label })
        } catch (e) {
            setItemData({ ...itemData, category: '', categoryName: '' })

        }
    }




    //onchange of the category input text, update the category search mode
    React.useEffect(() => {
        if (itemData.categoryInputText) {
            setSearchMode('dynamic');
        } else {
            setSearchMode('static')
        }
    }, [itemData.categoryInputText])

    const _scrollCatMenuToBottom = (e) => {
        e.preventDefault();
        if (itemCategoryOptionsMaxLength > defaultItemCategoryOptions.length && !itemData.categoryInputText) {
            store.dispatch({
                type: SET_FETCH_MORE_OPTIONS,
                payload: ['fetchMoreOptionsItemCategories', true]
            })
            getItemCategories_();
        }
    }

    const _itemDataChange = (e) => {
        const { name, type, value } = e.target;
        setItemData({ ...itemData, [name]: (type === 'number' ? parseFloat(value) : value) || '' })
        setErrors({ ...errors, [name]: '' })
    }

    const _removeImage = (e, index) => {
        e.stopPropagation();
        const tempImages = [...itemData.productImages];
        tempImages.splice(index, 1);
        setItemData({ ...itemData, productImages: tempImages })
    }

    const _deleteImage = (e, imageId) => {
        e.stopPropagation();
        const tempImages = [...itemData.receivedImages];
        const imgIndexToRemove = tempImages.indexOf(tempImages.find(img => img.id === imageId));
        console.log(imgIndexToRemove)
        tempImages.splice(imgIndexToRemove, 1)
        setItemData({ ...itemData, receivedImages: tempImages, removedImages: [...itemData.removedImages, imageId] });
    }


    const _toggleThumbnail = (index) => {
        const tempImages = [...itemData.productImages];
        tempImages[index] = {
            ...tempImages[index],
            category: tempImages[index].category === 'THUMBNAIL' ? 'PRODUCT' : 'THUMBNAIL'
        }
        setItemData({ ...itemData, productImages: tempImages })
    }


    const _productImagesAdd = async (e) => {
        setErrors({ ...errors, productImages: '' })
        const files = e.target.files;
        const encodedFiles = [...itemData.productImages]
        for (let file of files) {
            await getBase64(file).then((result) => {
                console.log("first this")
                encodedFiles.push({ base64: result, category: 'PRODUCT' })
            }).catch(e => console.log(e))
        }
        console.log('files here')
        setItemData({ ...itemData, productImages: encodedFiles })
        // await setItemData({ ...itemData, productImages: [...files] })
    }


    const _activeChange = (checked) => {
        console.log(checked);
        setItemData({ ...itemData, isActive: checked })
    }

    console.log(itemData.productImages)

    const _submit = () => {
        //check if there are errors

        const { isValid, errors } = validateItemCreation(itemData);

        if (!isValid) {
            console.log(errors);
            setErrors({ ...errors })
            return;
        };

        setSubmitting(true)

        const requestJson = {
            "action": "update",
            "item_id": itemData.id,
            "name": itemData.name,
            "sales_price": itemData.sp,
            "category": itemData.category,
            "description": itemData.description,
            "weight": parseFloat(itemData.weight) || 0,
            "weight_unit": itemData.weightUnit,
            "average_cost_price": itemData.cp,
            "product_images": itemData.productImages,
            'remove_image': itemData.removedImages,
            "barcode": itemData.barcode,
            "vat_enabled": itemData.vatable,
            "is_active": itemData.isActive
        }
        console.log(JSON.stringify(requestJson))
        //if no errros api call
        updateItem(requestJson)
            .then(async (res) => {
                await setSubmitting(false)
                console.log(res);
                if (res.json.status) {
                    Jhilikka.fire({ icon: 'success', title: 'Item updated successfully!', background: '#dffff0' })
                    props.history.push(`/item/${props.match.params.id}`);
                } else { Jhilikka.fire({ icon: 'error', title: res.json.error, background: '#ffe5de' }) }

            }).catch((err) => {
                console.log(err);
                setSubmitting(false)
            });
    }

    console.log(itemData)

    const ProductImagesList = itemData.productImages.map((instance, index) => {
        return <div key={index} onClick={() => _toggleThumbnail(index)}  >
            <img
                src={instance.base64}
                alt=""
                style={{
                    height: '60px',
                    minWidth: '60px',
                    maxWidth: '60px',
                    width: '60px',
                    objectFit: 'cover',
                    boxSizing: 'border-box',
                    border: instance.category === 'THUMBNAIL' ? '2px solid teal' : 'none'
                }}
            />
            <i onClick={(e) => _removeImage(e, index)}
                className="feather icon-x text-c-white f-15"
                style={{ color: 'white', cursor: 'pointer', backgroundColor: 'orangered', borderRadius: '3px', position: "relative", right: '16px', top: '-20px' }}
            />
        </div>
    })


    const ReceivedImagesList = itemData.receivedImages.map((instance, index) => {
        return <div key={index}  >
            <img
                src={DEMO.SERVER_URL + instance.image}
                alt=""
                style={{
                    height: '60px',
                    minWidth: '60px',
                    maxWidth: '60px',
                    width: '60px',
                    objectFit: 'cover',
                    boxSizing: 'border-box',
                    border: instance.category === 'THUMBNAIL' ? '2px solid teal' : 'none'
                }}
            />
            <i onClick={(e) => _deleteImage(e, instance.id)}
                className="feather icon-x text-c-white f-15"
                style={{ color: 'white', cursor: 'pointer', backgroundColor: 'red', borderRadius: '3px', position: "relative", right: '16px', top: '-20px' }}
            />
        </div>
    })


    console.log(itemData)

    if (loading) return <Loader />
    return (
        <Aux>
            <Row>
                <Col>
                    <Card>
                        <Card.Body>
                            <h5>Update Item</h5>
                            <hr />
                            <Row>


                                {!isEmpty(itemData.productImages) || !isEmpty(itemData.receivedImages) ? <Col md={12}>
                                    <Form.Group  >
                                        <Form.Label>Choose Thumbnail</Form.Label>
                                        <div className="d-flex justify-content-start">
                                            {ProductImagesList}
                                            <div style={{ border: '1px solid gray', padding: '1px', display: 'flex' }}>{ReceivedImagesList}</div>
                                        </div>
                                    </Form.Group >
                                </Col> : null
                                }


                                <Col md={6}>
                                    <Form.Group  >
                                        <Form.Label>Product Images*</Form.Label>
                                        <Form.File
                                            style={{ height: '41.98px' }}
                                            id="custom-file-translate-scss"
                                            label={itemData.productImages.length > 0 ? "Selected" : "Choose images"}
                                            onChange={_productImagesAdd}
                                            data-browse="Add image"
                                            lang="en"
                                            multiple={true}
                                            custom
                                        />
                                        <ErrorBody>{errors.productImages}</ErrorBody>
                                    </Form.Group>
                                </Col>
                                <Col md={6}>
                                    <Form.Group  >
                                        <Form.Label>Item Name*</Form.Label>
                                        <Form.Control name="name" value={itemData.name} onChange={_itemDataChange} type="text" placeholder="Item Name" />
                                        <ErrorBody>{errors.name}</ErrorBody>
                                    </Form.Group>
                                </Col>



                                <Col md={6}>
                                    <Form.Group  >
                                        <Form.Label>Description</Form.Label>
                                        <Form.Control name="description" as="textarea" rows="6" value={itemData.description} onChange={_itemDataChange} type="text" placeholder="Description" />
                                        <ErrorBody>{errors.description}</ErrorBody>
                                    </Form.Group>
                                </Col>
                                <Col md={6}>
                                    <Form.Group className="groupedReactSelect">
                                        <Form.Label>Category*</Form.Label>
                                        <InputGroup>
                                            <AsyncSelect
                                                placeholder='Select'
                                                isClearable={true}
                                                value={isEmpty(itemData.category) ? null : { label: itemData.categoryName, value: itemData.category }}
                                                onChange={(instance) => _selectedItemCategoryChange(instance)}
                                                defaultOptions={defaultItemCategoryOptions}
                                                loadOptions={loadItemCategoryOptions}
                                                cacheOptions={true}
                                                onInputChange={_categoryInputChange}
                                                components={{
                                                    MenuList: CustomSelectMenuList
                                                }}

                                                onMenuScrollToBottom={_scrollCatMenuToBottom}
                                                onMenuClose={() => {
                                                    store.dispatch({ type: SET_IS_MENU_OPEN, payload: ['account', false] })
                                                }}
                                                onMenuOpen={() => {
                                                    store.dispatch({ type: SET_IS_MENU_OPEN, payload: ['account', true] })
                                                }}
                                            />
                                            <InputGroup.Append
                                                onClick={() => setCreateItemCategoryModalData(true)}
                                            >
                                                <InputGroup.Text>
                                                    <i className="fa fa-plus p-t-3 f-15 m-r-0" />
                                                </InputGroup.Text>
                                            </InputGroup.Append>
                                        </InputGroup>
                                        <ErrorBody>{errors.category}</ErrorBody>
                                    </Form.Group>
                                    <Row>
                                        <Col md={6}>
                                            <Form.Group  >
                                                <Form.Label>Cost Price*</Form.Label>
                                                <Form.Control name="cp" value={itemData.cp} onChange={_itemDataChange} type="number" placeholder="Cost Price" />
                                                <ErrorBody>{errors.cp}</ErrorBody>
                                            </Form.Group>
                                        </Col>

                                        <Col md={6}>
                                            <Form.Group  >
                                                <Form.Label>Sales Price*</Form.Label>
                                                <Form.Control name="sp" value={itemData.sp} onChange={_itemDataChange} type="number" placeholder="Sales Price" />
                                                <ErrorTooltip error={errors.sp}>
                                                    {itemData.sp < itemData.cp ?
                                                        'Selling Price is less than the Cost Price!' :
                                                        null
                                                    }
                                                </ErrorTooltip>
                                                <ErrorBody>{errors.sp}</ErrorBody>
                                            </Form.Group>
                                        </Col>

                                    </Row>
                                </Col>

                                <Col md={6}>
                                    <Form.Label>Weight</Form.Label>
                                    <InputGroup className="mb-3">
                                        <Form.Control name="weight" value={itemData.weight} onChange={_itemDataChange} type="number" placeholder="Weight" />
                                        <Form.Control name="weightUnit" value={itemData.weightUnit} onChange={_itemDataChange} as="select">
                                            <option value='kg' >Kilograms</option>
                                            <option value='g' >Grams</option>
                                            <option value='lb' >LB</option>
                                        </Form.Control>
                                    </InputGroup>
                                </Col>
                                <ErrorBody>{errors.weightUnit}</ErrorBody>


                                <Col md={6}>
                                    <Form.Group  >
                                        <Form.Label>Barcode*</Form.Label>
                                        <Form.Control name="barcode" value={itemData.barcode} onChange={_itemDataChange} type="text" placeholder="Item Barcode" />
                                        <ErrorBody>{errors.barcode}</ErrorBody>
                                    </Form.Group>
                                </Col>

                                <Col md={3}>
                                    <Form.Group className="d-flex flex-column justify-content-end">
                                        <Form.Label name="vatable" style={{ marginBottom: '15px' }}>VATable?</Form.Label>
                                        <Switch
                                            onChange={() => { setItemData({ ...itemData, vatable: !itemData.vatable }) }}
                                            checked={itemData.vatable}
                                            width={75}
                                            uncheckedIcon={
                                                <div
                                                    style={{
                                                        display: "flex",
                                                        justifyContent: "center",
                                                        alignItems: "center",
                                                        height: "100%",
                                                        fontSize: 15,
                                                        color: "white",
                                                        paddingRight: 5
                                                    }}
                                                >
                                                    <b>NO</b>
                                                </div>
                                            }
                                            checkedIcon={
                                                <div
                                                    style={{
                                                        display: "flex",
                                                        justifyContent: "center",
                                                        alignItems: "center",
                                                        height: "100%",
                                                        fontSize: 15,
                                                        color: "white",
                                                        paddingLeft: 5
                                                    }}
                                                >
                                                    <b>YES</b>
                                                </div>
                                            }
                                            className="react-switch-square"
                                            id="icon-switch"
                                        />

                                    </Form.Group>
                                </Col>


                                <Col md={3}>
                                    <Form.Group className="d-flex flex-column justify-content-end">
                                        <Form.Label name="isActive" style={{ marginBottom: '15px' }}>Is Active?</Form.Label>
                                        <Switch
                                            onChange={_activeChange}
                                            checked={itemData.isActive}
                                            width={75}
                                            uncheckedIcon={
                                                <div
                                                    style={{
                                                        display: "flex",
                                                        justifyContent: "center",
                                                        alignItems: "center",
                                                        height: "100%",
                                                        fontSize: 15,
                                                        color: "white",
                                                        paddingRight: 5
                                                    }}
                                                >
                                                    <b>NO</b>
                                                </div>
                                            }
                                            checkedIcon={
                                                <div
                                                    style={{
                                                        display: "flex",
                                                        justifyContent: "center",
                                                        alignItems: "center",
                                                        height: "100%",
                                                        fontSize: 15,
                                                        color: "white",
                                                        paddingLeft: 5
                                                    }}
                                                >
                                                    <b>YES</b>
                                                </div>
                                            }
                                            className="react-switch-square"
                                            id="icon-switch"
                                        />
                                    </Form.Group>
                                </Col>





                            </Row>
                            <div className="d-flex justify-content-end" >
                                <Button
                                    variant="warning"
                                    onClick={() => { props.history.push('/items') }}
                                >
                                    Cancel
                                    </Button>
                                <Button
                                    disabled={submitting}
                                    variant="primary"
                                    onClick={_submit}
                                >
                                    {submitting ? <><i className="fa fa-spinner text-c-white f-15 fa-pulse m-l-1"></i>Updating</> : 'Update'}
                                </Button>
                            </div>

                        </Card.Body>
                    </Card>
                </Col>
                <CreateItemCategoryModal
                    show={createItemCategoryModalData}
                    onHide={() => setCreateItemCategoryModalData(false)}
                    setCreatedToSelected={(instance) => {
                        setItemData({
                            ...itemData,
                            category: instance.value,
                            categoryName: instance.label,
                        });
                        setDefaultItemCategoryOptions([{ ...instance }, ...defaultItemCategoryOptions])
                    }}
                />
            </Row>
        </Aux>
    );
}

export default Update;
